import React from 'react'

const Title = (props: { title: string | number | boolean | React.ReactElement<any, string | React.JSXElementConstructor<any>> | React.ReactFragment | React.ReactPortal | null | undefined }) => {
  return (
    <div className='title'>
        <h1>{props.title}</h1>
    </div>
  )
}

export default Title